<h2>Instance Properties</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>type</th>
<th>default</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>url</td>
<td>String</td>
<td></td>
<td><code v-pre>required</code>The url to a tileset JSON file.</td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td>true</td>
<td><code v-pre>optional</code>Determines if the tileset will be shown.</td>
</tr>
<tr>
<td>modelMatrix</td>
<td>Matrix4</td>
<td>Matrix4.IDENTITY</td>
<td><code v-pre>optional</code> A 4x4 transformation matrix that transforms the tileset's root tile.</td>
</tr>
<tr>
<td>shadows</td>
<td>ShadowMode</td>
<td>ShadowMode.ENABLED</td>
<td><code v-pre>optional</code> Determines whether the tileset casts or receives shadows from each light source.</td>
</tr>
<tr>
<td>maximumScreenSpaceError</td>
<td>Number</td>
<td>16</td>
<td><code v-pre>optional</code> The maximum screen space error used to drive level of detail refinement.</td>
</tr>
<tr>
<td>maximumMemoryUsage</td>
<td>Number</td>
<td>512</td>
<td><code v-pre>optional</code> The maximum amount of memory in MB that can be used by the tileset.</td>
</tr>
<tr>
<td>cullWithChildrenBounds</td>
<td>Boolean</td>
<td>true</td>
<td><code v-pre>optional</code> Optimization option. Whether to cull tiles using the union of their children bounding volumes.</td>
</tr>
<tr>
<td>dynamicScreenSpaceError</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre>optional</code> Optimization option. Reduce the screen space error for tiles that are further away from the camera.</td>
</tr>
<tr>
<td>dynamicScreenSpaceErrorDensity</td>
<td>Number</td>
<td>0.00278</td>
<td><code v-pre>optional</code> Density used to adjust the dynamic screen space error, similar to fog density.</td>
</tr>
<tr>
<td>dynamicScreenSpaceErrorFactor</td>
<td>Number</td>
<td>4.0</td>
<td><code v-pre>optional</code> A factor used to increase the computed dynamic screen space error.</td>
</tr>
<tr>
<td>dynamicScreenSpaceErrorHeightFalloff</td>
<td>Number</td>
<td>0.25</td>
<td><code v-pre>optional</code> A ratio of the tileset's height at which the density starts to falloff.</td>
</tr>
<tr>
<td>skipLevelOfDetail</td>
<td>Boolean</td>
<td>true</td>
<td><code v-pre>optional</code> Optimization option. Determines if level of detail skipping should be applied during the traversal.</td>
</tr>
<tr>
<td>baseScreenSpaceError</td>
<td>Number</td>
<td>1024</td>
<td><code v-pre>optional</code> When skipLevelOfDetail is true, the screen space error that must be reached before skipping levels of detail.</td>
</tr>
<tr>
<td>skipScreenSpaceErrorFactor</td>
<td>Number</td>
<td>16</td>
<td><code v-pre>optional</code> When skipLevelOfDetail is true, a multiplier defining the minimum screen space error to skip. Used in conjunction with skipLevels to determine which tiles to load.</td>
</tr>
<tr>
<td>skipLevels</td>
<td>Number</td>
<td>1</td>
<td><code v-pre>optional</code> When skipLevelOfDetail is true, a constant defining the minimum number of levels to skip when loading tiles. When it is 0, no levels are skipped.</td>
</tr>
<tr>
<td>immediatelyLoadDesiredLevelOfDetail</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre>optional</code> When skipLevelOfDetail is true, only tiles that meet the maximum screen space error will ever be downloaded.</td>
</tr>
<tr>
<td>loadSiblings</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre>optional</code> When skipLevelOfDetail is true, determines whether siblings of visible tiles are always downloaded during traversal.</td>
</tr>
<tr>
<td>clippingPlanes</td>
<td>ClippingPlaneCollection</td>
<td></td>
<td><code v-pre>optional</code> The ClippingPlaneCollection used to selectively disable rendering the tileset.</td>
</tr>
<tr>
<td>classificationType</td>
<td>ClassificationType</td>
<td></td>
<td><code v-pre>optional</code> Determines whether terrain, 3D Tiles or both will be classified by this tileset. See Cesium3DTileset#classificationType for details about restrictions and limitations.</td>
</tr>
<tr>
<td>ellipsoid</td>
<td>Ellipsoid</td>
<td>Ellipsoid.WGS84</td>
<td><code v-pre>optional</code> The ellipsoid determining the size and shape of the globe.</td>
</tr>
<tr>
<td>pointCloudShading</td>
<td>Object</td>
<td></td>
<td><code v-pre>optional</code> Options for constructing a PointCloudShading object to control point attenuation based on geometric error and lighting.</td>
</tr>
<tr>
<td>imageBasedLightingFactor</td>
<td>Cartesian2</td>
<td>new Cartesian2(1.0, 1.0)</td>
<td><code v-pre>optional</code> Scales the diffuse and specular image-based lighting from the earth, sky, atmosphere and star skybox.</td>
</tr>
<tr>
<td>lightColor</td>
<td>Cartesian3</td>
<td></td>
<td><code v-pre>optional</code> The color and intensity of the sunlight used to shade models.</td>
</tr>
<tr>
<td>debugFreezeFrame</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre>optional</code> For debugging only. Determines if only the tiles from last frame should be used for rendering.</td>
</tr>
<tr>
<td>debugColorizeTiles</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre>optional</code> For debugging only. When true, assigns a random color to each tile.</td>
</tr>
<tr>
<td>debugWireframe</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre>optional</code> For debugging only. When true, render's each tile's content as a wireframe.</td>
</tr>
<tr>
<td>debugShowBoundingVolume</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre>optional</code> For debugging only. When true, renders the bounding volume for each tile.</td>
</tr>
<tr>
<td>debugShowContentBoundingVolume</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre>optional</code> For debugging only. When true, renders the bounding volume for each tile's content.</td>
</tr>
<tr>
<td>debugShowViewerRequestVolume</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre>optional</code> For debugging only. When true, renders the viewer request volume for each tile.</td>
</tr>
<tr>
<td>debugShowGeometricError</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre>optional</code> For debugging only. When true, draws labels to indicate the geometric error of each tile.</td>
</tr>
<tr>
<td>debugShowRenderingStatistics</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre>optional</code> For debugging only. When true, draws labels to indicate the number of commands, points, triangles and features for each tile.</td>
</tr>
<tr>
<td>debugShowMemoryUsage</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre>optional</code> For debugging only. When true, draws labels to indicate the texture and geometry memory in megabytes used by each tile.</td>
</tr>
<tr>
<td>debugShowUrl</td>
<td>Boolean</td>
<td>false</td>
<td><code v-pre>optional</code> For debugging only. When true, draws labels to indicate the url of each tile.</td>
</tr>
<tr>
<td>isZoomTo</td>
<td>Boolean</td>
<td>true</td>
<td><code v-pre>optional</code> Determines whether zoomTo the tile.</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>Refer to the official document: <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/Cesium3DTileset.html">Cesium3DTileset</a></strong></li>
</ul>
<h2>Events</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and the cesiumObject.</td>
</tr>
<tr>
<td>allTilesLoaded</td>
<td></td>
<td>The event fired to indicate that all tiles that meet the screen space error this frame are loaded. The tileset is completely loaded for this view.This event is fired at the end of the frame after the scene is rendered.</td>
</tr>
<tr>
<td>initialTilesLoaded</td>
<td></td>
<td>The event fired to indicate that all tiles that meet the screen space error this frame are loaded. This event is fired once when all tiles in the initial view are loaded.This event is fired at the end of the frame after the scene is rendered.</td>
</tr>
<tr>
<td>loadProgress</td>
<td></td>
<td>The event fired to indicate progress of loading new tiles. This event is fired when a new tile is requested, when a requested tile is finished downloading, and when a downloaded tile has been processed and is ready to render.The number of pending tile requests, numberOfPendingRequests, and number of tiles processing, numberOfTilesProcessing are passed to the event listener.This event is fired at the end of the frame after the scene is rendered.</td>
</tr>
<tr>
<td>tileFailed</td>
<td></td>
<td>The event fired to indicate that a tile's content failed to load.If there are no event listeners, error messages will be logged to the console.</td>
</tr>
<tr>
<td>tileLoad</td>
<td></td>
<td>The event fired to indicate that a tile's content was loaded.The loaded Cesium3DTile is passed to the event listener.</td>
</tr>
<tr>
<td>tileUnload</td>
<td></td>
<td>The event fired to indicate that a tile's content was unloaded.The unloaded Cesium3DTile is passed to the event listener.This event is fired immediately before the tile's content is unload</td>
</tr>
<tr>
<td>tileVisible</td>
<td></td>
<td>This event fires once for each visible tile in a frame. This can be used to manually style a tileset.The visible Cesium3DTile is passed to the event listener.</td>
</tr>
<tr>
<td>mousedown</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>Triggered when the mouse is pressed on this primitive.</td>
</tr>
<tr>
<td>mouseup</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>Triggered when the mouse bounces on the primitive.</td>
</tr>
<tr>
<td>click</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>Triggered when the mouse clicks on the primitive.</td>
</tr>
<tr>
<td>dblclick</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>Triggered when the left mouse button double-clicks the primitive.</td>
</tr>
<tr>
<td>mousemove</td>
<td>{button,surfacePosition,target,type,windowPosition}</td>
<td>Triggered when the mouse moves to this primitive.</td>
</tr>
</tbody>
</table>
<hr>
</section></template>

<script>
    export default {
      data() {
        return {
          url: 'https://zouyaoji.top/vue-cesium/statics/SampleData/Cesium3DTiles/Tilesets/Tileset/tileset.json',
          options: [
            {
              value: 'https://zouyaoji.top/vue-cesium/statics/SampleData/Cesium3DTiles/Tilesets/Tileset/tileset.json',
              label: 'tileset one'
            },
            {
              value: 'https://zouyaoji.top/vue-cesium/statics/SampleData/Cesium3DTiles/Hierarchy/BatchTableHierarchy/tileset.json',
              label: 'tileset two'
            }
          ]
        }
      },
      methods: {
        ready(cesiumInstance) {
          this.cesiumInstance = cesiumInstance
          const { Cesium, viewer } = cesiumInstance
        },
        readyPromise(tileset) {
          const { Cesium, viewer } = this.cesiumInstance
          viewer.zoomTo(tileset, new Cesium.HeadingPitchRange(0.0, -0.5, tileset.boundingSphere.radius * 2.0))
        },
        allTilesLoaded() {
          console.log('All tiles are loaded')
        },
        initialTilesLoaded() {
          console.log('Initial tiles are loaded')
        },
        loadProgress(numberOfPendingRequests, numberOfTilesProcessing) {
          if (numberOfPendingRequests === 0 && numberOfTilesProcessing === 0) {
            console.log('Stopped loading')
            return
          }

          console.log('Loading: requests: ' + numberOfPendingRequests + ', processing: ' + numberOfTilesProcessing)
        },
        tileFailed(error) {
          console.log('An error occurred loading tile: ' + error.url)
          console.log('Error: ' + error.message)
        },
        tileLoad(tile) {
          console.log('A tile was loaded.')
        },
        tileUnload(tile) {
          console.log('A tile was unloaded from the cache.')
        },
        tileVisible(tile) {}
      }
    }
  </script>